body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
	margin: 0px auto;
	background-color: #3598DB;
	background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.25)), to(rgba(255,255,255,0.95)));
	background-image: -webkit-linear-gradient(rgba(255,255,255,0.25), rgba(255,255,255,0.95));
	background-image: linear-gradient(rgba(255,255,255,0.25), rgba(255,255,255,0.95));
/*	background-image: linear-gradient(rgba(255,255,255,0.7), rgba(255,255,255,0.9)), url(/images/johannes-hofmann-536086-unsplash.jpg);*/
	/*background-position: center;*/
  /*background-repeat: no-repeat;*/
  /*background-size: cover;*/
  background-attachment: fixed;
}

.heading {
	background-color: #173B5D;
	padding: 10px 0;
	box-shadow: 0px 18px 30px black;
}

.title {
	font-size: 25px;
	padding: 20px 0;
	font-family: 'Corben', cursive;
	font-size: 50px;
}

.togcapsule1 {
	padding: 20px 20px 5px 0px;
}

.togcapsule2 {
	padding: 20px 0px 5px 20px;
}

.flex-container {
  display: flex;
  justify-content: center;
}

.mainbox {
	padding: 40px 80px;
}

.footer {
	background-color: #173B5D;
	text-align: center;
	padding: 10px;
	color: #E6E6E6;
	font-size: 14px;
}
.wholecontent {
	padding: 20px;
	background-color: white;
	border-radius: 25px;
	opacity: 0.9;
	font-family: 'Roboto', sans-serif;
}
label {
	padding-bottom: 10px;
	padding-top: 10px;
	width: 100%;
}
.list {
	padding: 0px 40px 10px 40px;
}
#speech {
  border: none;
  padding: 0;
  border-radius: 100%;
  width: 80px;
  height: 80px;
  font-size: 1.5em;
  color: #fff;
  padding: 0;
  margin: 0;
  background: #17A2B8;
  position: relative;
  line-height: 50px;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-image: none; }

.pulse-ring {
  content: '';
  width: 80px;
  height: 80px;
  border: 5px solid #0a454e;
  border-radius: 50%;
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-animation: pulsate infinite 2s;
          animation: pulsate infinite 2s; }

@-webkit-keyframes pulsate {
  0% {
    -webkit-transform: scale(1, 1);
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.2, 1.2);
    opacity: 0; } }

.form-rounded {
  border-radius: 1rem; }

.remove {
	text-align: right;
}

.story {
	padding: 10px;
}
